@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --bg-colour-1: #536976;
  --bg-colour-2: #292e49;
}
.body {
  padding: 20px;
  height: 100%;
  overflow-y: auto;
}

.open-nav {
  position: absolute;
  bottom: 2em;
}

.nav {
  place-self: center;
}

.nav-inner {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 4px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
  box-shadow: none;
}

.nav--active .nav-inner {
  box-shadow: 25px 0px 60px 28px rgba(15, 137, 194, 0.6);
  border: 4px solid gray;
}

.nav-ball {
  --x-position: 0;
  --y-position: 0;
  position: absolute;
  /* half of the hight of the outer circle subtract half the height of the circle */
  margin-top: calc(100px - 20px);
  z-index: 99;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  touch-action: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: translate(var(--x-position), var(--y-position));
}

.nav--selected .nav-ball {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
}

.nav-ball-inner {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background-color: rgb(165, 158, 158);
  pointer-events: none;
}

.nav--active .nav-ball-inner {
  animation: pulse 2s infinite;
}

.nav-pip {
  transition: "0.5s all";
  animation: fadeIn 0.5s normal;
}

@keyframes pulse {
  0%,
  100% {
    background-color: rgb(165, 158, 158);
  }
  50% {
    background-color: rgba(165, 158, 158, 0.5);
  }
}


.area {
  perspective: 600px;
}

.cube {
  position: relative;
  transform-style: preserve-3d;
  transform: rotateX(var(--rotate-y)) rotateY(var(--rotate-x))
    rotateZ(var(--rotate-z)) translateZ(calc(var(--rectangle-height) / -2));

  width: var(--rectangle-width);
  height: var(--rectangle-height);
  transition: all 0.1s;
}

.orbit {
  width: 600px;
  height: 600px;
  border-radius: 75%;
  border: 4px solid rgba(176, 175, 207, 0.548);
  position: absolute;
}

