.cube-face-front {
  position: absolute;
  width: var(--rectangle-width);
  height: var(--rectangle-height);
  border: 1px solid rgba(176, 175, 207, 0.548);
}

.cube-face-side {
  position: absolute;
  width: var(--rectangle-height);
  height: var(--rectangle-height);
  border: 1px solid rgba(176, 175, 207, 0.548);
}

.cube-face--front {
  transform: rotateY(0deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(128, 128, 128, 0.432);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cube-face--back {
  transform: rotateY(180deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0);
}

.cube-face--right {
  transform: rotateY(90deg) translateZ(calc(0.75 * var(--rectangle-width)));
  background: rgba(0, 0, 0, 0);
}

.cube-face--left {
  transform: rotateY(270deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}

.cube-face--top {
  transform: rotateX(90deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}

.cube-face--bottom {
  transform: rotateX(-90deg) translateZ(calc(var(--rectangle-height) / 2));
  background: rgba(0, 0, 0, 0, 0);
}
.about-page {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  --rectangle-width: 300px;
  --rectangle-height: 150px;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0.5em 0;
}

.resume {
  margin: 0 auto;
  padding: 10px 15px;
  background-color: white;
  font-size: 12px;
  box-shadow: 0px 0px 7.5px 0px rgba(0, 0, 0, 0.75);
  max-width: 21cm;
}

ul {
  margin: 0;
  /* padding: 0 0 0 1em; */
}

ul > li {
  margin-bottom: 0.25em;
}

ul:last-child {
  margin: 0;
}

.workplace > p {
  margin-top: 0;
}

.header {
  margin: 0 0 1em 0;
}

.section {
  margin-top: 0.5em;
}

.section:first-of-type {
  margin-top: 0;
}

.experience-node {
  display: flex;
  align-items: center;
  margin-top: 0.5em;
}

.header-socials {
  background-color: lightgray;
  margin: 10px -15px;
  padding: 10px 15px;
  display: flex;
  justify-content: space-evenly;
  overflow-wrap: anywhere;
}

.header-socials > p {
  display: inline-block;
  text-align: center;
}

.label {
  font-weight: 800;
}

.date {
  font-style: italic;
  font-weight: 400;
  color: gray;
}

.circle-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-self: stretch;
  align-items: center;
  margin: 0 10px 0 0;
}

.circle {
  width: 30px;
  height: 30px;
  border-radius: 60%;
  border: 2px solid gray;
}

.circle-container::before {
  content: "";
  border: 1px solid gray;
  height: calc(100% / 2 - 30px);
}

.circle-container::after {
  content: "";
  border: 1px solid gray;
  height: calc(100% / 2 - 30px);
}

.heading {
  width: 100%;
}

.gradient-text {
  background-color: rgb(142, 142, 142);
  background-image: linear-gradient(
    to right,
    #536976,
    #292e49);
  background-image: linear-gradient(
    to right,
    var(--bg-colour-1, #536976),
    var(--bg-colour-2, #292e49)
  );
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.bottom-border {
  height: 2px;
  background: #536976;
  background: linear-gradient(
    to right,
    #536976,
    #292e49);
  background: linear-gradient(
    to right,
    var(--bg-colour-1, #536976),
    var(--bg-colour-2, #292e49)
  );
}

h1 {
  margin-top: 0.75em;
  margin-bottom: 1em;
}

.technologies {
  display: flex;
  justify-content: space-between;
}

.margin-heading {
  margin-top: 0.5em;
}

@media print {
  @page {
    size: 21cm 29.7cm;
  }
  .resume {
    -webkit-print-color-adjust: exact;
    height: 100%;
    width: 100%;
    position: fixed;
    box-shadow: none;
    top: 0;
    left: 0;
  }

  .header-socials {
    margin: 10px -15px;
  }
}

.page {
  display: grid;
  grid-template: auto / 1fr 6fr;
  height: 100vh;
  background: red;
  background: linear-gradient(
    to right,
    #536976,
    #292e49);
  background: linear-gradient(
    to right,
    var(--bg-colour-1, #536976),
    var(--bg-colour-2, #292e49)
  );
  --rotate-x: 0deg;
  --rotate-y: 0deg;
  --rotate-z: 0deg;
}

@media (max-width: 800px) {
  .page {
    grid-template: auto / auto;
  }
}

.mobile-nav-wrapper {
  overflow: hidden;
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 99;
  background-color: rgba(56, 59, 59, 0.85);
  display: grid;
  animation: fadeIn 0.3s normal;
}

.corner-button {
  overflow: hidden;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 200px;
  width: 200px;
  border-radius: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 25px 0px 60px 28px rgba(15, 137, 194, 0.6);
  border: 4px solid gray;
  transform: translate(50px, 50px);
}

.pip {
  background-color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  text-align: center;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
}

.button {
  border: none;
}

.button:hover {
  cursor: pointer;
}

.page-nav--active {
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
